
body {
  display: grid;
  height: 100vh;
  margin: 0;
  grid-template-rows: auto 1fr;
  font-family: system-ui, -apple-system;
}

header {
  border: 5px solid orange;
  padding: 10px;
}

.intro {
  padding: 15px;

  li {
    margin-top: .5rem;
    margin-bottom: .5rem;
  }
}

.container {
  position: relative;
  max-height: 100vw;
  background-image: radial-gradient(#fff, #ccc);
  border: 5px solid green;

  canvas {
    position: absolute;
    width: 100%;
    height: 100%;
  }
}

.controls {
  padding: 10px;
  border: 5px solid blue;
}

*[hidden] {
  display: none !important;
}

label {
  font-weight: bold;
}

#debugmenu {
  margin: 10px 0 0 0;
  padding: 10px 0 0 0;
  border-top: 5px solid blue;
}

#directions {
  margin: 10px 0 0 0;
  padding: 10px 0 0 0;
  border-top: 5px solid blue;

  li {
    display: inline;

    &:not(:last-child)::after {
      content: ';'
    }

    &:last-child::after {
      content: '.'
    }
  }
}
